'use client';
import React, { useEffect, useState, useRef } from 'react';
import Head from 'next/head';
import { Chart, registerables } from 'chart.js';
Chart.register(...registerables);
import { fetchData } from '../../services/backendService1';

// Interfaces for type safety
interface KPI {
  rowKey: string;
  value: string;
  status?: string;
}

interface PlotDataPoint {
  x: number;
  y: number;
  exceedsThreshold: boolean;
}

interface TableDataPoint {
  id: string;
  timePeriod: string;
  meanPrediction?: number;
  error?: number;
  percentageError?: number;
  status: string;
}

interface OutletsExceedingThreshold {
  id: string;
  y_true: number;
  y_pred: number;
  percentage_error: number;
}

interface ErrorDataState {
  plotData: PlotDataPoint[];
  tableData: TableDataPoint[];
}

interface Coverage {
  total_points?: number;
  warning_coverage?: number;
  drift_coverage?: number;
}

interface Clusters {
  warning?: string[];
  drift?: string[];
  combined?: string[];
}

interface BackwardAnalysis {
  backward_10_percent_drift?: boolean;
  backward_10_percent_warning?: boolean;
}

export default function Mode2Page(): React.ReactElement {
  const chartRef = useRef<Chart | null>(null);
  const [kpis, setKpis] = useState<KPI[]>([]);
  const [errors, setErrors] = useState<ErrorDataState>({ plotData: [], tableData: [] });
  const [outletsExceedingThreshold, setOutletsExceedingThreshold] = useState<OutletsExceedingThreshold[]>([]);
  const [state, setState] = useState<string>('Unknown');
  const [coverage, setCoverage] = useState<Coverage>({});
  const [clusters, setClusters] = useState<Clusters>({ warning: [], drift: [], combined: [] });
  const [backwardAnalysis, setBackwardAnalysis] = useState<BackwardAnalysis>({});
  const [currentPeriod, setCurrentPeriod] = useState<string>('N/A');
  const [totalOutlets, setTotalOutlets] = useState<number>(0);
  const [outletsExceedingThresholdCount, setOutletsExceedingThresholdCount] = useState<number>(0);
  const [xaiExplanation, setXaiExplanation] = useState<string>('No explanation available');
  const [loading, setLoading] = useState<boolean>(true);
  const [backendError, setBackendError] = useState<string | null>(null);

  // Fetch Data from Backend
  const initData = async (): Promise<void> => {
    setLoading(true);
    setBackendError(null);
    try {
      const {
        kpis: fetchedKpis,
        errors: fetchedErrors,
        outletsExceedingThreshold: fetchedOutlets,
        state: fetchedState,
        coverage: fetchedCoverage,
        clusters: fetchedClusters,
        backwardAnalysis: fetchedBackwardAnalysis,
        currentPeriod: fetchedCurrentPeriod,
        totalOutlets: fetchedTotalOutlets,
        outletsExceedingThresholdCount: fetchedOutletsExceedingThresholdCount,
        xaiExplanation: fetchedXai,
      } = await fetchData();

      // Build plotData from indices object in clusters if available, else fallback to previous method
      const indices = (fetchedClusters as any).indices;
      let plotData: PlotDataPoint[] = [];

      if (indices && indices.normal && indices.warning && indices.drift) {
        indices.normal.forEach((idx: number) => {
          plotData.push({ x: idx, y: 0, exceedsThreshold: false });
        });
        indices.warning.forEach((idx: number) => {
          plotData.push({ x: idx, y: 1, exceedsThreshold: false });
        });
        indices.drift.forEach((idx: number) => {
          plotData.push({ x: idx, y: 2, exceedsThreshold: false });
        });
        plotData.sort((a, b) => a.x - b.x);
      } else {
        const warningSet = new Set(fetchedClusters.warning || []);
        const driftSet = new Set(fetchedClusters.drift || []);
        plotData = fetchedErrors.plotData.map((item: PlotDataPoint, index: number) => {
          let yValue = 0; // normal
          if (warningSet.has(item.x.toString())) {
            yValue = 1;
          } else if (driftSet.has(item.x.toString())) {
            yValue = 2;
          }
          return { x: index, y: yValue, exceedsThreshold: item.exceedsThreshold };
        });
      }

      setKpis(fetchedKpis);
      setErrors({ plotData, tableData: fetchedErrors.tableData });
      setOutletsExceedingThreshold(fetchedOutlets);
      setState(fetchedState);
      setCoverage(fetchedCoverage);
      setClusters(fetchedClusters);
      setBackwardAnalysis(fetchedBackwardAnalysis);
      setCurrentPeriod(fetchedCurrentPeriod);
      setTotalOutlets(fetchedTotalOutlets);
      setOutletsExceedingThresholdCount(fetchedOutletsExceedingThresholdCount);
      setXaiExplanation(fetchedXai || 'No explanation available');
    } catch (error) {
      console.error('Error fetching data:', error);
      if (error instanceof Error) {
        setBackendError(`Failed to load data: ${error.message}`);
      } else {
        setBackendError('Failed to load data: Unknown error');
      }
    } finally {
      setLoading(false);
    }
  };

  React.useEffect(() => {
    initData();
  }, []);

  return (
    <div className="bg-gray-900 min-h-screen flex flex-col">
      <Head>
        <title>Mode 2 | Business Dashboard</title>
      </Head>
      <main className="flex-grow container mx-auto px-4 py-8">
        {/* Backend Error Section */}
        {backendError && (
          <div className="bg-red-900/50 border border-red-700 rounded-lg p-4 mb-6">
            <div className="flex items-center">
              <svg className="h-5 w-5 text-red-300 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
                />
              </svg>
              <h3 className="text-lg font-medium text-red-300">Backend Error</h3>
            </div>
            <p className="mt-2 text-red-200">{backendError}</p>
            <p className="mt-2 text-red-200 text-sm">Displaying fallback data. Some features may be limited.</p>
            <button
              onClick={() => initData()}
              className="mt-3 px-4 py-2 bg-red-800/50 hover:bg-red-800 text-white rounded-md text-sm font-medium"
            >
              Retry Connection
            </button>
          </div>
        )}

        {/* Header Section */}
        <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 mb-6 border border-gray-700">
          <h2 className="text-2xl font-semibold text-blue-300 mb-4">OCTAVE - RG Dashboard</h2>
          <p className="text-blue-200 mb-2">Current Period: {loading ? 'Loading...' : currentPeriod}</p>
          <p className="text-blue-200 mb-2">State: {loading ? 'Loading...' : state}</p>
          <p className="text-blue-200 mb-2">Total Outlets: {loading ? 'Loading...' : totalOutlets}</p>
          <p className="text-blue-200 mb-2">Outlets Exceeding Threshold: {loading ? 'Loading...' : outletsExceedingThresholdCount}</p>
          <p className="text-blue-200 mb-2">Coverage - Total Points: {loading ? 'Loading...' : coverage.total_points ?? 'N/A'}</p>
          <p className="text-blue-200 mb-2">Coverage - Warning: {loading ? 'Loading...' : coverage.warning_coverage ?? 'N/A'}</p>
          <p className="text-blue-200 mb-2">Coverage - Drift: {loading ? 'Loading...' : coverage.drift_coverage ?? 'N/A'}</p>
          <p className="text-blue-200 mb-2">Backward Analysis - 10% Drift: {loading ? 'Loading...' : backwardAnalysis.backward_10_percent_drift ? 'Yes' : 'No'}</p>
          <p className="text-blue-200 mb-2">Backward Analysis - 10% Warning: {loading ? 'Loading...' : backwardAnalysis.backward_10_percent_warning ? 'Yes' : 'No'}</p>

          {/* Key Metrics */}
          <div className="grid grid-cols-1 md:grid-cols-5 gap-4 mt-4">
            {kpis.map((kpi) => (
              <div key={kpi.rowKey} className="bg-blue-900/30 p-4 rounded-lg border border-blue-800/50">
                <h3 className="text-lg font-medium text-blue-200 mb-2">{kpi.rowKey}</h3>
                <p
                  className={`text-xl ${
                    kpi.status === 'Warning' ? 'text-yellow-400' : kpi.status === 'Error' ? 'text-red-400' : 'text-green-400'
                  }`}
                >
                  {loading ? 'Loading...' : kpi.value || 'N/A'}
                </p>
              </div>
            ))}
          </div>
        </div>

        {/* Drift/Warning Plot Section */}
        <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 mb-6 border border-gray-700">
          <h2 className="text-2xl font-semibold mb-4">Drift/Warning Plot</h2>
          <div className="h-64 bg-gray-700 rounded p-4">
            {loading ? (
              <div className="flex items-center justify-center h-full">
                <div className="text-white">Loading plot data...</div>
              </div>
            ) : (
              <canvas id="driftWarningChart" className="w-full h-full" ref={(el) => {
                if (el && !loading) {
                  const ctx = el.getContext('2d');
                  if (ctx) {
                    if (chartRef.current) {
                      chartRef.current.destroy();
                    }
                    chartRef.current = new Chart(ctx, {
                      type: 'line',
                      data: {
                        labels: errors.plotData.map((item) => item.x),
                        datasets: [
                          {
                            label: 'Drift State',
                            data: errors.plotData.map((item) => item.y),
                            borderColor: 'rgb(75, 192, 192)',
                            backgroundColor: errors.plotData.map((item) => {
                              if (item.y === 0) return 'rgba(54, 162, 235, 0.5)'; // normal - blue
                              if (item.y === 1) return 'rgba(255, 206, 86, 0.5)'; // warning - yellow
                              if (item.y === 2) return 'rgba(255, 99, 132, 0.5)'; // drift - red
                              return 'rgba(201, 203, 207, 0.5)'; // default grey
                            }),
                            borderWidth: 2,
                            tension: 0.1,
                            fill: true,
                            stepped: true,
                          },
                        ],
                      },
                      options: {
                        responsive: true,
                        maintainAspectRatio: false,
                        plugins: {
                          legend: {
                            labels: {
                              color: '#e5e7eb',
                            },
                          },
                          tooltip: {
                            mode: 'nearest',
                            intersect: true,
                            callbacks: {
                              label: function(context) {
                                const y = context.parsed.y;
                                if (y === 0) return 'Normal';
                                if (y === 1) return 'Warning';
                                if (y === 2) return 'Drift';
                                return 'Unknown';
                              }
                            }
                          },
                        },
                        scales: {
                          x: {
                            title: {
                              display: true,
                              text: 'Time Period',
                              color: '#93c5fd',
                              font: {
                                weight: 'bold',
                              },
                            },
                            grid: {
                              color: 'rgba(255, 255, 255, 0.1)',
                            },
                            ticks: {
                              color: '#e5e7eb',
                              font: {
                                size: 12,
                              },
                            },
                          },
                          y: {
                            title: {
                              display: true,
                              text: 'Drift State',
                              color: '#93c5fd',
                              font: {
                                weight: 'bold',
                              },
                            },
                            beginAtZero: true,
                            ticks: {
                              stepSize: 1,
                              callback: function (this: Scale, tickValue: string | number) {
                                const value = typeof tickValue === 'string' ? parseInt(tickValue) : tickValue;
                                if (value === 0) return 'Normal';
                                if (value === 1) return 'Warning';
                                if (value === 2) return 'Drift';
                                return '';
                              },
                              color: '#e5e7eb',
                              font: {
                                size: 12,
                              },
                            },
                            grid: {
                              color: 'rgba(255, 255, 255, 0.1)',
                            },
                            min: 0,
                            max: 2,
                          },
                        },
                        animation: {
                          duration: 1000,
                          easing: 'easeInOutQuad',
                        },
                        elements: {
                          point: {
                            radius: 5,
                            hoverRadius: 7,
                          },
                        },
                      },
                    });
                  }
                }
              }}
              />
            )}
          </div>
        </div>

        {/* Error Tables Section */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Error Comparison */}
          <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 border border-gray-700">
            <h2 className="text-2xl font-semibold text-blue-300 mb-4">Error Comparison</h2>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-700">
                <thead>
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-blue-200 uppercase tracking-wider">ID</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-blue-200 uppercase tracking-wider">Time Period</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-red-400 uppercase tracking-wider">Error</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-700">
                  {errors.tableData.slice(0, 5).map((error, index) => (
                    <tr key={index}>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-white">{error.id}</td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-white">{error.timePeriod}</td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-red-400">
                        {(error.error ?? 0).toFixed(2)}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>

          {/* Threshold Exceedances */}
          <div className="bg-red-900/20 rounded-xl shadow-md overflow-hidden p-6 border border-red-800/50">
            <h2 className="text-2xl font-semibold text-red-300 mb-4">Threshold Exceedances</h2>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-red-800/50">
                <thead>
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-red-200 uppercase tracking-wider">ID</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-red-200 uppercase tracking-wider">True Value</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-red-200 uppercase tracking-wider">Predicted Value</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-red-200 uppercase tracking-wider">Percentage Error</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-red-800/50">
                  {outletsExceedingThreshold.slice(0, 5).map((outlet) => (
                    <tr key={outlet.id}>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-white">{outlet.id}</td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-white">{outlet.y_true.toFixed(2)}</td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-white">{outlet.y_pred.toFixed(2)}</td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-red-400">{outlet.percentage_error.toFixed(2)}%</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>

          {/* XAI Result Section */}
          <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 mb-6 border border-gray-700">
            <h2 className="text-2xl font-semibold text-blue-300 mb-4">XAI Result</h2>
            <div className="space-y-3 text-white">
              {loading ? (
                <p>Loading XAI explanation...</p>
              ) : xaiExplanation ? (
                <div dangerouslySetInnerHTML={{ __html: xaiExplanation }} />
              ) : (
                <p className="text-red-400">No explanation available</p>
              )}
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
